.center-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.page-body {
  font-family: "Roboto Condensed Light", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: rgb(16, 38, 84);
  height: 100%;
}

.page-main {
  flex: 1;
  margin: 0;
  padding: 0;
}

.main-block {
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg);
  margin-bottom: 120px;
  padding: 30px 45px 38px;
}
@media (max-width: 1023px) {
  .main-block {
    padding: 38px 45px 77px;
    margin-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .main-block {
    padding: 17px 15px 83px;
    margin-bottom: 65px;
  }
}

.main-block__inner {
  padding: 30px 45px 0;
}

.main-block__header {
  display: flex;
  justify-content: space-between;
  max-width: 1163px;
  margin: 0 auto;
}

.main-block__header p {
  padding: 0 4px 0 0;
  margin: 0;
}
@media (max-width: 1023px) {
  .main-block__header p {
    padding: 0;
    margin-bottom: 18px;
  }
}

@media (max-width: 1023px) {
  .main-block__header svg {
    width: 81px;
  }
}
@media (max-width: 767px) {
  .main-block__header svg {
    width: 67px;
  }
}

.main-block__header a:last-of-type {
  font-family: "Bebas Neue Bold", "Arial", sans-serif;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.03em;
  color: #FFFFFF;
}
.main-block__header a:last-of-type:hover {
  color: rgb(16, 75, 163);
}
.main-block__header a:last-of-type:active {
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .main-block__header a:last-of-type {
    font-size: 24px;
    line-height: 24px;
  }
}
@media (max-width: 767px) {
  .main-block__header a:last-of-type {
    font-size: 20px;
    line-height: 20px;
  }
}

.main-block__flex-container {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  transform: translateY(-5px);
  max-width: 1187px;
  margin: 0 auto;
}
@media (max-width: 1218px) {
  .main-block__flex-container {
    transform: unset;
  }
}

.main-block__text-block {
  transform: translateX(-11px);
  text-align: right;
  margin-right: 52px;
  padding-top: 60px;
}
@media (max-width: 1023px) {
  .main-block__text-block {
    padding-top: 10px;
    margin-right: 5px;
    transform: translateX(-26px);
  }
}
@media (max-width: 767px) {
  .main-block__text-block {
    transform: translate(-40px, 3px);
    margin: 0 auto;
  }
}

.main-block__text-block button {
  font-family: "Roboto Condensed Bold", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  min-width: 230px;
  min-height: 57px;
  text-transform: uppercase;
  border: 4px solid #FFFFFF;
  background-color: transparent;
  color: #FFFFFF;
  cursor: pointer;
}
.main-block__text-block button:hover {
  border: 4px solid rgb(230, 19, 4);
  color: rgb(230, 19, 4);
}
.main-block__text-block button:active {
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .main-block__text-block button {
    min-width: 183px;
  }
}
@media (max-width: 767px) {
  .main-block__text-block button {
    min-width: 290px;
    min-height: 59px;
  }
}

.main-block__text-block-inner {
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-block__text-block-inner ul {
  font-family: "Roboto Condensed Regular", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  font-size: 14px;
  line-height: 21px;
  color: #FFFFFF;
  transform: translate(37px, 78px);
  max-width: 130px;
  min-width: 120px;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .main-block__text-block-inner ul {
    transform: translate(20px, 59px);
  }
}
@media (max-width: 767px) {
  .main-block__text-block-inner ul {
    transform: translate(31px, 52px);
  }
}

.main-block__text-block-inner h1 {
  font-family: "Bebas Neue Bold", "Arial", sans-serif;
  font-size: 109px;
  line-height: 109px;
  color: #FFFFFF;
  text-transform: uppercase;
  letter-spacing: 0.03em;
  -webkit-text-stroke: 2px white;
  -webkit-text-fill-color: white;
  max-width: 300px;
  margin: 0 0 66px;
}
@media (max-width: 1023px) {
  .main-block__text-block-inner h1 {
    font-size: 86px;
    line-height: 86px;
    transform: translateY(-3px);
    margin-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .main-block__text-block-inner h1 {
    font-size: 78px;
    line-height: 78px;
    margin-bottom: 48px;
  }
}

.main-block__text-block-inner span {
  font-family: "Bebas Neue Regular", "Arial", sans-serif;
  font-size: 49px;
  line-height: 59px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  -webkit-text-stroke: 0;
  display: inline-block;
  max-width: 130px;
}
@media (max-width: 1023px) {
  .main-block__text-block-inner span {
    font-size: 34px;
    line-height: 41px;
  }
}
@media (max-width: 767px) {
  .main-block__text-block-inner span {
    font-size: 28px;
    line-height: 34px;
  }
}

.main-block__picture {
  margin-right: 189px;
}
@media (max-width: 1270px) {
  .main-block__picture {
    margin-right: calc(100vw - 1100px);
  }
}
@media (max-width: 1120px) {
  .main-block__picture {
    margin-right: 20px;
  }
}
@media (max-width: 1023px) {
  .main-block__picture {
    margin: 0;
  }
}
@media (max-width: 767px) {
  .main-block__picture {
    display: none;
  }
}

@media (max-width: 1023px) {
  .main-block__picture img {
    object-fit: cover;
    height: 389px;
  }
}

.main-block__languages {
  align-self: flex-end;
  padding-bottom: 39px;
}
@media (max-width: 1023px) {
  .main-block__languages {
    display: none;
  }
}

.main-block__languages p {
  margin: 0 0 10px;
}

.main-block__languages a {
  color: #FFFFFF;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 14px;
}
.main-block__languages a:hover {
  color: rgb(16, 75, 163);
}
.main-block__languages a:active {
  opacity: 0.5;
}

.gym {
  padding: 0 45px;
}
@media (max-width: 1023px) {
  .gym {
    padding-bottom: 16px;
  }
}
@media (max-width: 767px) {
  .gym {
    padding: 0;
    padding-bottom: 70px;
  }
}

.gym__inner {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1163px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .gym__inner {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
}

.gym__text-block {
  max-width: 355px;
  max-width: 415px;
  padding: 44px 0 0 60px;
}
@media (max-width: 1023px) {
  .gym__text-block {
    max-width: 502px;
    text-align: center;
    padding: 0;
    margin-bottom: 38px;
  }
}
@media (max-width: 767px) {
  .gym__text-block {
    padding: 0 13px;
    margin-bottom: 35px;
  }
}
@media (max-width: 380px) {
  .gym__text-block {
    text-align: left;
  }
}

.gym__text-block h2 {
  font-family: "Bebas Neue Bold", "Arial", sans-serif;
  font-size: 49px;
