}
@media (max-width: 1023px) {
  .gym__text-block h3 {
    margin: 0 0 31px;
  }
}
@media (max-width: 767px) {
  .gym__text-block h3 {
    margin: 0 0 23px;
  }
}

.gym__text-block p {
  line-height: 25px;
  margin: 0;
}
.gym__text-block p:first-of-type {
